label {
    color: white;
    margin-bottom: 1rem;
}
form {
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;
}

form input, form textarea {
    margin-bottom: 1rem;
    padding: 10px 18px;
    font-size: 1rem;
    background-color:#08090c;
    border-color:#ce293c6c;
    color: #FAF5F4;
}