.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:2rem;
    position: fixed;
    width: 100%;
    height:  100px;
    z-index: 10;
    }
    .header-bg {
        background-color: #07080a;
        transition: 0.5s;
    }
    
    .nav-menu {
        display:flex;
    
    }
    .nav-menu li {
        padding: 0 1rem;
    }
    .nav-menu li a{
        font-size: 1.2rem;
        font-weight: 550;
    }
    .hamburguer{
        display: none;
    }
    
    @media screen and (max-width:1040px) {
        .nav-menu
        {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100vh;
            background-color:  #0c0d11 ;
            position: absolute;
            top:0;
            left:-100%;
            z-index: -3;
            transition: 0.5s;
        }
        .nav-menu.active {
            left: 0;
        }
        .nav-menu li {
            padding: 1rem 0;
    
        }
        .nav-menu li a {
            font-size: 2rem;
        }
        .hamburguer{
            display: initial;
        }
    }