.footer {
    width: 100%;
    padding: 2rem 0;
    background-color: #07080a;
}

.footer-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat (2, 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location {
    display: flex;
    margin-bottom: 0.5rem;
}

.location p {
    line-height: 23px;
}


.right {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social {
    margin-top: 1.2rem;

}
p{
    font-size: 15px;
    padding-bottom: 0.8rem;
}

@media screen and (max-width:640px) {
    .footer-container {
        grid-template-columns: 1fr;

    }
}

